import React from "react";
import { Button } from "./Button";
import { Link } from "react-router-dom";
import './Footer.css';

function Footer() {
  return (
    <div className="footer-container">
      <section className="footer-subscription">
        <p className="footer-subscription-heading">
          Join the Adventure newsletter to receive our best vacation deals
        </p>
        <p className="footer-subscription-text">
          You can unsubscribe at any time.
        </p>
        <div className="input-areas">
          <form>
            <input
              type="email"
              className="footer-input"
              placeholder="Your Mail"
            />
            <Button buttonStyle="btn--outline">Subscribe</Button>
          </form>
        </div>
      </section>
      <div className="footer-link">
        <div className="footer-link-wrapper">
          <div className="footer-link-items">
            <h2>About Us</h2>
            <Link to="/sign-up">How it works</Link>
            <Link to="/">Testominals</Link>
            <Link to="/">Careers</Link>
            <Link to="/">investors</Link>
            <Link to="/">Terms of services</Link>
          </div>
          <div className="footer-link-items">
            <h2>Contact Us</h2>
            <Link to="/sign-up">How it works</Link>
            <Link to="/">Testominals</Link>
            <Link to="/">Careers</Link>
            <Link to="/">investors</Link>
            <Link to="/">Terms of services</Link>
          </div>
          <div class="footer-link-items">
            <h2>Videos</h2>
            <Link to="/">Submit Video</Link>
            <Link to="/">Ambassadors</Link>
            <Link to="/">Agency</Link>
            <Link to="/">Influencer</Link>
          </div>
          <div class="footer-link-items">
            <h2>Social Media</h2>
            <Link to="/">Instagram</Link>
            <Link to="/">Facebook</Link>
            <Link to="/">Youtube</Link>
            <Link to="/">Twitter</Link>
          </div>
        </div>
      </div>
      <section className="social-media">
        <div className="social-media-wrap">
          <Link to="/" className="social-logo">
            ROUTE
            <i className="fab fa-typo3" />
          </Link>
          <small class="website-rights">ROUTE Â© 2021</small>
        </div>
       
        <div className="social-icons">
          <Link
            className="social-icon-links-facebook"
            to="/"
            target="_blank"
            area-label="Facebook"
          ><i className="fab fa-facebook"></i></Link>
          <Link
            className="social-icon-links-instagram"
            to="/"
            target="_blank"
            area-label="Instagram"
            ><i className="fab fa-instagram"></i></Link>
          <Link
            className="social-icon-links-youtube"
            to="/"
            target="_blank"
            area-label="youtube"
            ><i className="fab fa-youtube"></i></Link>
          <Link
            className="social-icon-links-linkedin"
            to="/"
            target="_blank"
            area-label="Linkedin"
            ><i className="fab fa-linkedin"></i></Link>
        </div>
      </section>
    </div>
  );
}

export default Footer;
